import React, { useState } from 'react'; 
import './App.css';


import 'bootstrap/dist/css/bootstrap.min.css';
import {
	Container, 
	Row, 
	Col,
	InputGroup,
	FormControl,
	Button,
	Table,
	Alert
} from 'react-bootstrap';

function App() {

	const [token, setToken] = useState(null);
	const [refreshing, setRefreshing] = useState(false);
	const [startDate, setStartDate] = useState(null);
	const [endDate, setEndDate] = useState(null);
	const [winners, setWinners] = useState(null);
	const [winnerCount, setWinnerCount] = useState(20);
	const [stats, setStats] = useState({
		round: {
			cans: 0,
			unique:0,
			surveys: 0
		},
		total: {
			cans: 0,
			unique: 0,
			surveys: 0
		}
	});

	const drawWinner = async(existing) => {
		let exclude = existing.map(item => item.customer);

		let dateQuery = startDate + " 00:01:00," + endDate + " 23:59:00";
		let _entries = await fetch('https://compo-api.huskypet.co.za/husky-microsite/items/entries?access_token=' + 
								token + 
								'&sort=?&filter[created_on][between]=' + dateQuery + '&filter[customer][nin]=' + exclude.join(',') + '&limit=1'
		).catch(e => alert("Unable to communicate with API"));

		let _entriesJSON = await _entries.json();

		if(_entriesJSON) {
			return _entriesJSON.data[0];
		}
	}

	const selectWinners = async () => {

		if(token == null) {
			alert("Don't forget the token!");
			return;
		}
		if(startDate == null || endDate == null) {
			alert("Please select start and end date of round first");
			return;
		}

		setRefreshing(true);

		let dateQuery = startDate + " 00:01:00," + endDate + " 23:59:00"; 
		
		let _winnerList = [];
		// draw our winners individuall for uniqueness
		for(let i = 0; i < winnerCount; i++) {
			let winner = await drawWinner(_winnerList);
			
			if (!winner) {
				// we've reached the max
				break;
			}

			// add so that we don't draw again
			_winnerList.push(winner);
		}
		
		
		if(_winnerList) { 
			let consumerList = _winnerList.map(e => e.customer).join(','); 

			// fetch consumers
		 	let _consumers = await fetch('https://compo-api.huskypet.co.za/husky-microsite/items/consumers?access_token=' + token + '&filter[id][in]=' + consumerList + '&limit=' + winnerCount)
		 						   .catch(e => alert("Unable to communicate with API"));
			let _consumersJSON = await _consumers.json(); 
			 
			// fetch files
			let fileList = _winnerList.map(e => e.receipt).join(','); 
			let _receipts = await fetch('https://compo-api.huskypet.co.za/husky-microsite/files?access_token=' + token + '&filter[id][in]=' + fileList)
								  .catch(e => alert("Unable to communicate with API"));
			let _receiptsJSON = await _receipts.json();  

			let _combined = [];
			
			for(let k = 0; k < _consumersJSON.data.length; k++) {
				for(let i = 0; i < _winnerList.length; i++) {
					if (_consumersJSON.data[k].id === _winnerList[i].customer) {
						for(let p = 0; p < _receiptsJSON.data.length; p++) {
							if (_receiptsJSON.data[p].id === _winnerList[i].receipt) {
								_combined.push({
									'consumer': _consumersJSON.data[k],
									'entry': _winnerList[i],
									'receipt': _receiptsJSON.data[p].data.full_url
								});
							}
						}
					}
				}
			} 

		 	setWinners(_combined);
		} else {
			setWinners(null);
		}
		
		setRefreshing(false);
	};

	const refresh = async () => {
		if(token == null) {
			alert("Don't forget the token!");
			return;
		}
		if(startDate == null || endDate == null) {
			alert("Please select start and end date of round first");
			return;
		}

		setRefreshing(true);

		let newStat = stats;

		let dateQuery = startDate + " 00:01:00," + endDate + " 23:59:00";
		let allDateQuery = "2020-06-13 00:01:00," + endDate + " 23:59:00";

		// Get this rounds surveys
		let roundSurvey = await fetch('https://compo-api.huskypet.co.za/husky-microsite/items/survey?access_token=' + token + '&filter[created_on][between]=' + dateQuery + '&limit=-1')
								.catch(e => alert("Unable to communicate with API"));
		// Get total survey
		let totalSurvey = await fetch('https://compo-api.huskypet.co.za/husky-microsite/items/survey?access_token=' + token + '&filter[created_on][between]=' + allDateQuery + '&limit=-1')
								.catch(e => alert("Unable to communicate with API"));


		// get round entries
		let roundEntries = await fetch('https://compo-api.huskypet.co.za/husky-microsite/items/entries?access_token=' + token + '&filter[created_on][between]=' + dateQuery + '&limit=-1')
								.catch(e => alert("Unable to communicate with API"));
		// get total entries
		let totalEntries = await fetch('https://compo-api.huskypet.co.za/husky-microsite/items/entries?access_token=' + token + '&filter[created_on][between]=' + allDateQuery + '&limit=-1')
								.catch(e => alert("Unable to communicate with API"));

		if (roundSurvey && roundSurvey.ok &&
			totalSurvey && totalSurvey.ok &&
			roundEntries && roundEntries.ok &&
			totalEntries && totalEntries.ok) {
			let roundSurveyJSON = await roundSurvey.json(); 
			let totalSurveyJSON = await totalSurvey.json();
		
			let roundEntriesJSON = await roundEntries.json();
			let totalEntriesJSON = await totalEntries.json();

			// Survey results
			newStat.round.surveys = roundSurveyJSON.data.length;
			newStat.total.surveys = totalSurveyJSON.data.length;

			// Cans
			newStat.round.cans = roundEntriesJSON.data.length;
			newStat.total.cans = totalEntriesJSON.data.length; 
			
			// Entries
			let _customerIds = roundEntriesJSON.data.map(e => {
				return e.customer;
			}); 
			newStat.round.unique = Array.from(new Set(_customerIds)).length; // isolate unique
			_customerIds = totalEntriesJSON.data.map(e => {
				return e.customer;
			});
			newStat.total.unique = Array.from(new Set(_customerIds)).length; // isolate unique
		} 

		setRefreshing(false);
	}

	return (
		<div className="App">
			<Container>
				<Row>
					<Col> 
						<InputGroup className="mb-3">
							<InputGroup.Prepend>
								<InputGroup.Text id="basic-addon3">
									Security token (ask Kurt):
								</InputGroup.Text>
							</InputGroup.Prepend>
							<FormControl id="basic-url" aria-describedby="basic-addon3" onChange={(e) => setToken(e.target.value)} />
						</InputGroup>
					</Col>
				</Row>
				<Row>
					<Col>
						<p>
							Select the first day this round starts and the last day of the round.<br/>
							<small>(System will automatically select 00:01 and 23:59 from first/last)</small>
						</p>
					</Col>
				</Row>
				<Row>
					<Col>
						<InputGroup className="mb-3">
							<InputGroup.Prepend>
								<InputGroup.Text>First Day</InputGroup.Text>
  							</InputGroup.Prepend>
							<FormControl onChange={(e) => setStartDate(e.target.value)} type="date" />
						</InputGroup>
					</Col>
					<Col>
						<InputGroup className="mb-3">
							<InputGroup.Prepend>
								<InputGroup.Text>Last Day</InputGroup.Text>
  							</InputGroup.Prepend>
							<FormControl onChange={(e) => setEndDate(e.target.value)} type="date" />
						</InputGroup>
					</Col>
					<Col>
						<Button block variant="primary" onClick={() => refresh() }>Refresh</Button>
					</Col>
				</Row>
				<Row>
					<Col><hr/></Col>
				</Row>
				<Row>
					<Col>
						{refreshing ? (
							<p>Loading...</p>
						) : (
							<>
								<p>Selected Round</p>
								<Table striped bordered hover>
									<thead>
										<tr>
											<th>Total Entries (Cans)</th>
											<th>Round Unique Entries</th>
											<th>Total Survey Results</th> 
										</tr>
									</thead>
									<tbody>
										<tr>
											<td>{ stats.round.cans }</td>
											<td>{ stats.round.unique }</td>
											<td>{ stats.round.surveys }</td>
										</tr>
									</tbody>
								</Table>
								
								<p>Since Start Until Round Close</p>
								<Table striped bordered hover>
									<thead>
										<tr>
											<th>Total Entries (Cans)</th>
											<th>Unique Entries</th>
											<th>Total Survey Results</th>
										</tr>
									</thead>
									<tbody>
										<tr>
											<td>{ stats.total.cans }</td>
											<td>{ stats.total.unique }</td>
											<td>{ stats.total.surveys }</td>
										</tr>
									</tbody>
								</Table>
							</>
						)}
					</Col>
				</Row>
			</Container>
			<hr/>
			<Container>
				<Row>
					<Col>
						<p>The winners below are randomly selected from the week selected above.</p>
					</Col>
				</Row>
				<Row>
					<Col>
						<InputGroup className="mb-3">
							<InputGroup.Prepend>
								<InputGroup.Text>No. Winners</InputGroup.Text>
  							</InputGroup.Prepend>
							<FormControl value={winnerCount} onChange={(e) => setWinnerCount(e.target.value)} type="number" />
						</InputGroup>

						<Button onClick={() => selectWinners()}>Select Winners</Button>
					</Col>
				</Row>
				<Row>
					<p>&nbsp;</p>
				</Row>
				<Row>
					{refreshing ? (
							<p>Loading...</p>
						) : (
							<>
								<Table style={{'textAlign' : 'left'}}>
									<thead>
										<tr>
											<th>Name</th>
											<th>Email</th>
											<th>Cell</th>
											<th>Entry date</th>
											<th>Receipt</th>
										</tr>
									</thead>
									<tbody>
										{winners && winners.map((winner, i) => ( 
										<tr key={i}> 
											<td>{ winner.consumer.name_surname }</td>
											<td>{ winner.consumer.email }</td>
											<td>{ winner.consumer.mobile }</td>
											<td>{ winner.entry.created_on }</td>
											<td><a href={ winner.receipt } target="_blank">View</a></td>
										</tr>
										))}
									</tbody>
								</Table>
							</>
						)
					}
				</Row>
			</Container>
		</div>
	);
}

export default App;
